<!-- 聊天 -->
<template>
  <div class="chat">
    <div class="top">
      <div class="back" @click="back"></div>
      通知消息
    </div>
    <div class="content">
      <div class="msg-item-left msg-item">
        <h5>6月1日 上午11:28</h5>
        <div class="main">
          <div class="avatar">
            <img src="../../assets/images/icon/kibana.png" alt="">
          </div>
          <div class="msg-content">
            <h6>———— 系 统 消 息  ————</h6>
            <p>MQ队列管理服务器(外高桥)-客户消息队列出现堆积，当前值3000（80%），请联系系统三部确认。</p>
          </div>
        </div>
      </div>
      <div class="msg-item-right msg-item">
        <h5>6月1日 上午11:28</h5>
        <div class="main">
          <div class="avatar">
            <img src="../../assets/images/user@2x.png" alt="">
          </div>
          <div class="msg-content">
            <p>建议优化页面布局</p>
          </div>
        </div>
      </div>
    </div>
    <div class="input-bar">
      <div class="textarea" ref="textarea" contenteditable="true"></div>
      <div class="submit" @click="submit">发送</div>
      <!-- <input type="text" placeholder="请键入您对该视图的建议"> -->
    </div>
  </div>
</template>

<script>
import { getSocket } from '../../assets/js/socket';
export default {
  name: "MessageChat",
  data() {
    return {
      wsData: {}, // 保存 websocket 数据对象
      form: { // 表单
        name: '',
        age: ''
      }
    };
  },
  created() {
    this.getSocketData();
  },
  mounted() {},
  methods: {
    back() {
      this.$router.go(-1);
    },
    // 获取数据
    getSocketData() {
      let params = this.form;
      getSocket(
        `ws://path`,
        JSON.stringify(params),
        (data, ws) => {
            console.log(data, ws);
            // 保存数据对象, 以便发送消息
            this.wsData = ws;
        }
      );
    },
    // 发送数据
    sendSocketData() {
      let params = this.form;
      params.name = 'xx';
      params.age = '18';
      this.wsData.send(JSON.stringify(params));
    },
    submit() {
      const value = this.$refs.textarea.innerText.trim();
      if (value) {
        this.$refs.textarea.innerHTML = '';
      }
    }
  },
  beforeDestroy() {
    this.wsData.close(); // 关闭 websocket
  }
};
</script>
<style lang='less' scoped>
/* @import url(); 引入css类 */
.chat{
  height: 100%;
}
.top{
  width: 100%;
  height: 0.5rem;
  border-bottom: 1px solid #EAEAEA;
  line-height: 0.5rem;
  text-align: center;
  background: #fff;
  font-size: 0.16rem;
  font-weight: 500;
  color: #232323;
  position: fixed;
  .back{
    width: 0.4rem;
    position: absolute;
    height: 100%;
    background: url('../../assets/images/icon_return@2x.png') no-repeat center center;
    background-size: 0.2rem 0.2rem;
  }
}
.content{
  padding: 0.5rem 0;
  min-height: 100%;
  background: #eeeeee;
  box-sizing: border-box;
  .msg-item{
    overflow: hidden;
    margin-top: 0.2rem;
    h5{
      font-size: 0.14rem;
      text-align: center;
      color: #A6A6A6;
      margin-bottom: 0.16rem;
    }
    .main{
      overflow: hidden;
      .avatar{
        float: left;
        width: 0.4rem;
        height: 0.4rem;
        background: #fff;
        border-radius: 4px;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
          width: 100%;
        }
      }
      .msg-content{
        float: left;
        max-width: 2.5rem;
        margin: 0 0.1rem;
        padding: 0.1rem 0.12rem;
        font-size: 0.16rem;
        color: #3A3A3A;
        border-radius: 4px;
        line-height: 0.22rem;
        box-sizing: border-box;
        h6{
          text-align: center;
        }
      }
    }
  }
  .msg-item-left{
    padding-left: 0.12rem;
    .msg-content{
      background: #fff;
    }
  }
  .msg-item-right{
    padding-right: 0.12rem;
    .main{
      .avatar{
        float: right;
        img{
          height: 90%;
          width: auto;
        }
      }
      .msg-content{
        float: right;
        background: #66DAD6;
      }
    }
  }
}
.input-bar{
  min-height: 0.5rem;
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #F1F1F1;
  box-shadow: 0 0 4px 0 rgba(0,0,0,0.11);
  padding: 0.07rem 0.1rem;
  box-sizing: border-box;
  .textarea{
    float: left;
    outline: none;
    min-height: 0.2rem;
    line-height: 0.2rem;
    font-size: 0.14rem;
    padding: 0.08rem 0.15rem;
    padding-right: 0.42rem;
    border-radius: 0.18rem;
    background: #fff url('../../assets/images/icon_keyboard@2x.png') no-repeat 2.6rem 0.02rem;
    background-size: 0.32rem 0.32rem;
    width: 3rem;
    box-sizing: border-box;
    -webkit-user-select:text
  }
  .textarea:empty:before{ 
    content: '请键入您对该视图的建议'; 
    color: gray; 
  } 
  .textarea:focus:before{
    content:none;
  }
  .submit{
    float: right;
    margin-top: 0.04rem;
    width: 0.5rem;
    height: 0.28rem;
    font-size: 0.14rem;
    color: #ffffff;
    background: #45a4b1;
    line-height: 0.28rem;
    text-align: center;
  }
}
</style>